import React from "react"
import ContentWrapper from "./ContentWrapper"
import ArticleImage from "./ArticleImage"
import ItemContainer from "./ItemContainer"
import { withRouter } from "react-router"
import { Link } from "react-router-dom"
import data from "../data"
import ScrollToTop from "./ScrollToTop"
import "./Article.css"

const Article = ({ match }) => {
  const id = parseInt(match.params.id, 10)
  const project = data[id - 1]
  const { title } = project
  return (
    <ContentWrapper>
      <ScrollToTop />
      <div className="article-wrapper">
        <div className="article-title-wrapper serif">
          <div className="article-title">{title}</div>
        </div>
        <div className="article-text">
          {
            id === 1 && <div>
              <p>
                <ArticleImage src="/images/1.gif" />
              </p>
              <p>
                <b>TL;DR:</b> With <i>Stanley the Chatbot</i>, a job applicant can discover job openings, provide personal information, and conduct interviews via their favourite instant messaging apps including Facebook Messenger and WeChat and this service can be easily integrated with any Facebook or WeChat account of a company. <a href="https://www.messenger.com/t/letthetalenttalk" target="_blank" rel="noopener noreferrer">Click here</a> to interact with a Facebook Messenger version of Stanley the Chatbot. This chatbot helps Talkpush (the company that creates <i>Stanley the Bot</i>) recruit talents. Also, you can <a href="https://talkpush.com/chatbot.html" target="_blank" rel="noopener noreferrer">click here</a> to visit the official page of <i>Stanley the Chatbot</i> for more details.
              </p>
              <p>
                For companies and organizations that interview high volumes of candidates, like call centers, hotels and business process outsourcing companies, arranging and conducting interviews and managing information is a real pain which soaks up a great deal of time and resources. Conventionally, phone interview is thought to be the best option, with which HR personnel are allocated to make hundreds of phone calls tediously asking the same questions.
              </p>
              <p>
                <i>Talkpush</i>, a Manila-based startup invested by <i>500 startups</i>, aims to leverage technologies to develop a better solution. When the company was still based in Hong Kong, I joined the team and worked as the major developer of <i>Stanley the Bot</i> which acts as a core part of the solution.
              </p>
              <p>
                When I first joined the team, our solution was mainly based on phone calls. But Talkpush will help clients automate the process. Applicants would send their CV via email or job board and then answer some prerecorded questions over the phone and their profiles would be automatically generated with their answers as well as the information extracted from their CV. Although this is a great improvent compared to interviews conducted by human, it left much to be desired.
              </p>
              <p>
                At that time, Facebook just lauched their chatbot platform, so we decided to give it a try and I took the responsibility of developing the chatbot. This was a challenging task as there was little information about developing such a product both from design and engineering prospective. Basically, we need to figure all things out by ourselves.
              </p>
              <p>
                After about a month of intensive development, the first beta version was developed released:
                <iframe width="560" height="315" className="article-video"  src="https://www.youtube.com/embed/Ct2Iks0co64" frameborder="0" allowfullscreen></iframe>
              </p>
              <p>
                The service was developed with the excellent object-oriented programming language, <a href="https://www.ruby-lang.org/" target="_blank" rel="noopener noreferrer">Ruby</a>. It was connected with an Facebook app we created at <a href="https://developers.facebook.com/" target="_blank" rel="noopener noreferrer">Facebook for Developers</a> via a webhook endpoint from our <a href="http://rubyonrails.org/" target="_blank" rel="noopener noreferrer">Rails</a> application.
              </p>
              <p>
                The basic idea is that our client (companies that recruit) would connect their Facebook account to our service and the chatbot will take over their messenger which means that unless the chatbot is disabled under certain circumstances, whenever someone talks to their Facebook account, he would be actually talking to <i>Stanley</i> (the chatbot). Facebook will relay relevant data to our Rails application, then <i>Stanley</i> will determine what does the message mean, who sent it, what's the current state of his application and etc, and then generate replies based on all these informatio and instruct Messenger to reply them.
              </p>
              <p>
                If an applicant wants to apply for a job, the chatbot will display a carousel of job openings according to the campaigns that recruiters created in the Talkpush system. After choosing a campaign, the applicant will asked to enter their personal information like phone numbers (if he has already provided it before, we will identify him and skip this step). Then he can choose either to be called immediately or schedule a call to take an interview. Later on, I upgraded the chatbot so that an applicant can record audio and video directly with Messenger to take the interview. And I also developed the WeChat version of <i>Stanley</i>.
              </p>
              <p>
                As a developer, I was also actively involved in the design process as the team maintains a relatively flat structure. With this project, I really witnessed the difficult chanllenges posed in regard to user experience and the significance of good UX design. As <i>Stanley the Bot</i> is probably the first of its kind as a recruiting tool, different people have different problems interacting with it and we cannot foresee most of them. For example, although <i>Stanley</i> would clearly indicate that he's not an actual human being, many applicants still fail to interact with it according to instructions but talk to him freely as they would when talking to a real person. While we tried hard to bring AI capability to address this problem, we were also able to mitigate it by redesigning the experience. For example, the chatbot would abdicate by itself and notify the applicant that an actual person would take care of him later on when he has responded improperly during the same state for 3 times.
              </p>
              <p>
                Besides the applicants, the major end users who interact with the chatbot, companies that recruits are also users of the chatbot. What's amazing about the chatbot is that, these companies can set up and even customize the chatbot effortlessly without any IT personnel involved. All they need to do is to log into Talkpush system, click a button to connect their Facebook account and authorize us, then check which modules they'd like to enable (e.g. if they want to enable in-app interview directly, they can just check the corresponding checkbox) and it's done. Even if the client has special needs we cannot satisfy with existing modules, we can write a custom module and implement within a day. It is designed to be flexible (functions can be appended and removed easily) as our client always have different business needs. Once set up, <i>Stanley</i> would work 24-7 talking to applicants and generating profiles. This frees recruiters from making phone calls and let them focus on processes that really require their expertise.
              </p>
            </div>
          }
          {
            id === 2 && <div>
              <p>
                <ArticleImage src="/images/2.gif" />
              </p>
              <p>
                (Before you read this article, you might want to read about <Link to='/portfolio/1'>Stanley the Chatbot</Link> first)
              </p>
              <p>
                With <i>Stanley the Chatbot</i>, even before recruiters making any effort to contact applicants, there are already hundreds of profiles with personal information, voices and videos waiting for them to screen. Then the recruiters need a tool to inspect and screen the candidate and communicate with them if needed.
              </p>
              <p>
                So we developed this single page application called <i>Unified Box</i>. I was one of the major developers who proposed the technical solution (<a href="https://github.com/shakacode/react_on_rails" target="_blank" rel="noopener noreferrer">react_on_rails</a>) and was responsible for developing the candidate list within the left panel and most of the middle panel where recruiters chat with candidates.
              </p>
              <p>
                In our system, candidates are categorized according to the status of their applications. Recruiters can switch between categories by clicking the small icons on the left of the UI. Then they can click on a candidate within the list to talk to him. Marking, labeling with custom text and filtering are also implemented within the left panel.
              </p>
              <p>
                After a candidate is selected, the recruiter can talk to him via whatever channel (Messenger, WeChat, SMS, E-mail) that is available. They can create templates for frequent content. And when they do so, they can insert variables. They make a template like "Hello, @candidate_name. Thanks for your interest in @position_name" and the system will process the text properly according to the context.
              </p>
              <p>
                In the right panel comes the profile of the candidate and operations a recruiter can perform upon a candidate. After evaluating the candidate with his information and interview performance, the recruiter can shortlist, reject, onhold or hire him by clicking corresponding button and then notifications will be automatically sent to the candidate via instant messaging app, SMS or email.
              </p>

            </div>
          }
          {
            id === 3 && <div>
              <p>
                <ArticleImage src="/images/3-1.png" />
              </p>
              <p>
                While e-commerce has revolutionized the way we shop, offline experience is still irreplacable in many circumstances. And <i>Omnitech</i> by <i>Tofugear</i> one of the solutions that attempt to utilize cutting-edge technologies to bring us a whole new off-line shopping experience.
              </p>
              <p>
                The <i>Omnitech</i> front end app we developed was implemented on interactive kiosks (we call them "smart mirrors") and tablets like iPads, acting as the main UI that interacts with customers. I was one of the two major developers. I supported the lead developer to refactor the app with Redux to enable more complex interactions while keeping the app easy to maintain and test. I also developed several features including the help screen which overlays on top of the UI and implemented several UI styles and animations based on designers' design.
              </p>
              <p>
                When a customer enters fitting room, our application will determine what products are brought in based on RFID and smart mirror placed in the fitting room will display detailed information about the product. So if the customer finds the pair of jeans doesn't fit, they don't step out of the room to fetch the shop assistant and wait for several minutes for him to check the inventory. They can now clearly view the inventory of other sizes on the smart mirror and simply click "Try On" button then the product will be brought to him. He can also view all the products within the store and "Try On" any of them and add it to the virtual shopping cart. Finally, they can check out with their preferred payment methods including PayPal, Apple pay, credit card and etc directly with the mirror rather than walk a long way to the cashier and wait in the queue to pay the bill.
              </p>
            </div>
          }
          {
            id === 4 && <div>
              <p>
                This app was developed for a bank to provide credit card offer information for customers. They wanted to substitute Flash app that serves the same purpose as there are security vulnerability issues with Flash and it's not friendly to mobile phones and it's hard to enable operations like copying in a Flash app.
              </p>
              <p>
                After my colleague laid the foundation of this app with jQuery, I took the responsibility of adjusting it to display the information with the dynamic data (XML) rather than hard-coding the information to be displayed.
              </p>
              <p>
                It was also my duty to make the app compatible with various devices including mobile phones, tablets and computers. To achieve this, I utilized CSS3 Media Queries to make the UI aware of and adapt to changes in window or device width.
              </p>
              <p>
                The first thing I did is to make some of the UI elements fluid, which means that they will have a relative size according to the size of the window or device. But that's not enough. Breaking changes in the UI also has to occur if certain point is reached. For example, when the app is displayed on the screen of a mobile phone or small-sized tablet, it is no longer proper to display a sidebar. So the app should transform it to a compact vertical list instead.
              </p>
              <p>
                <ArticleImage src="/images/4-1.gif" />
              </p>
              <p>
                As the result of the responsive design, the app would always display information nicely and clearly across different devices that varies in size.
              </p>
              <p>
                <ArticleImage src="/images/4-2.png" />
              </p>
            </div>
          }
        </div>
      </div>
      <div className="article-footer">
        <div className="serif">
          Read about other projects I've taken part in:
        </div>
        <ItemContainer currentProjectId={id} />
      </div>
    </ContentWrapper>
  )
}

export default withRouter(Article)



